.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 79%;
  height: 100%;
  padding: 41px 71px;
  gap: 50px;
}

.title {
  width: 100%;
  font-size: 24px;
  line-height: 36px;
}

.mainBlock {
  width: 100%;
  min-height: 812px;
  background: #FFFFFF;
  box-shadow: 0 10px 60px rgba(226, 236, 249, 0.5);
  border-radius: 30px;
  padding: 30px 40px 40px 38px;
}
@media screen and (max-width: 1367px) {
  .container {
    gap: 10px;
  }
  .mainBlock {
    min-height: 100%;
  }
}

@media screen and (max-width: 900px) {
  .container {
    position: relative;
    padding: 0;
  }
}
